import React from 'react';
import './staff.css';
import StaffData from './staffData';
import Tap from '../../media/icons/tap.svg';

const Staff = () => {

    return (
        <div className="Staff_section" id="faculty">
            <div className="utility_center_text">
                   <h2 className="about_section__heading">
                      faculties
                   </h2>
            </div>
            <div className="staff_card_container">
                 {
                     StaffData.map(teacher => {
                         return(
                            <div className="staff_card">
                            <div className="card_side card_side-front card_side-front-1">
                            <div className="card_tap_info">
                                <img className="card_tap-icon" src={Tap} alt="click"></img>
                            </div>
                                <img className="card-front--img" src={teacher.image} alt="image"/>
                                <div className="staff_name">{teacher.name}</div>
                            </div>
                            <div className="card_side card_side-back card_side-back-1">
                                <div className="backside--name">{teacher.name}</div>
                                <hr className="hr" />
                                <div className="backside--subject">{teacher.subject}</div>
                                <hr className="hr" />
                                <div className="backside--about">{teacher.about}</div>
                            </div>
                         </div>
                    
                         )
                     })
                 }
               </div>  
        </div>
    )
}
export default Staff;