import React,{useState} from 'react';
import './index.css'
import Logo from '../../media/logo.png';
import Menu from '../../media/icons/hamburger.svg';

const Navbar = () => {
  const [nav,setNav] = useState(false);
    return (
        <div>
         <nav className="navbar">
        <div className="brand-title">
        <img className="logo-img" style={{width:"65px"}} src={Logo} alt="logo"></img>
        </div>
        <a href="#" className="toggle-button" onClick={() => nav?setNav(false):setNav(true)}>
          <span className="bar"></span>
          <span className="bar"></span>
          <span className="bar"></span>
        </a>
        <div className={`${nav?'active':null} navbar-links`}>
          <ul>
            <li><a href="/">Home</a></li>
            <li><a href="#about">About</a></li>
            <li><a href="#courses">Course</a></li>
            <li><a href="#faculty">Faculty</a></li>
            <li><a href="#feedback">Feedback</a></li>
          </ul>
        </div>
      </nav>
        </div>
    )
}

export default Navbar;